	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Value Added Tax on Nothing","date":"26 Jan 2010","slug":"/2010-01-26-value-added-tax-on-nothing/","tags":"Y2010"},"html":"<p>I asked this question on Twitter and got a lot of guesses, so I'll try it here for the blog crowd. </p>\n<p>In February we're about to start another coffee cup giveaway with our McMenüs (Value Meals). Even though the cup is free, we have to adjust our Value Added Tax (similar to sales tax) programming in the registers. Why do you think we have to pay VAT on an item that is free?</p>\n<p>So you don't get distracted... yes, Germany has different VAT rates for restaurant food eaten in or taken out. A porcelain cup is not food, so is taxed the same regardless. The answer has nothing to do with eat-in vs take-out.</p>\n<p><strong>Update:</strong> The answer is <a href=\"http://www.papascott.de/archives/2010/01/26/value-added-tax-on-nothing/#comment-40677\">below</a> in the comments, for everyone curious about German VAT regulations!</p>"},"next":{"fields":{"title":"New Sensation","date":"11 Jan 2010","slug":"/2010-01-11-new-sensation/"}},"previous":{"fields":{"title":"Male Striptease...","date":"03 Feb 2010","slug":"/2010-02-03-male-striptease/"}}},{"node":{"fields":{"title":"New Sensation","date":"11 Jan 2010","slug":"/2010-01-11-new-sensation/","tags":"Y2010"},"html":"<p><a href=\"http://www.flickr.com/photos/51035717986@N01/4265253987\" title=\"View 'CIMG0841' on Flickr.com\"><img border=\"0\" width=\"500\" alt=\"CIMG0841\" src=\"http://farm3.static.flickr.com/2793/4265253987_fd7397cbcf.jpg\" /></a></p>\n<p>Time to bring PapaScott into the new decade (if you believe decades begin in '0 years and not '1 years, that is). I was going to write about how this is the first new year in business that we weren't rushing around with new things, but I guess it's a bit late for that now.</p>\n<p>We got caught on the edge of the snow storm that <a href=\"http://www.spiegel.de/international/europe/0,1518,671186,00.html\">hit northern Germany</a> for the weekend, making things really bad on the Baltic coast (<a href=\"http://www.spiegel.de/fotostrecke/fotostrecke-50586.html\">Spiegel Online has some pictures</a>). Only about 10 inches of snow, but the wind made the roads no fun yesterday. School was cancelled in all counties near us, so Christopher gets a snow day today.</p>\n<p>The German media doesn't know much about broadcasting weather warnings. Every podunk American local TV station has weather radar that shows the exact location of incoming storms. Here they can announce SHOCK HORROR DRAMA SNOW DISASTER is on the way, but can't say where, exactly. We got better storm information from a <a href=\"http://itunes.apple.com/de/app/regenradar/id293654490?mt=8\">free iPhone app</a> than from the public TV networks we pay for.</p>"},"next":{"fields":{"title":"Double Digits!","date":"27 Dec 2009","slug":"/2009-12-27-double-digits/"}},"previous":{"fields":{"title":"Value Added Tax on Nothing","date":"26 Jan 2010","slug":"/2010-01-26-value-added-tax-on-nothing/"}}},{"node":{"fields":{"title":"Double Digits!","date":"27 Dec 2009","slug":"/2009-12-27-double-digits/","tags":"Y2009"},"html":"<p><a href=\"http://www.flickr.com/photos/51035717986@N01/4218675472\" title=\"View 'CIMG0803' on Flickr.com\"><img border=\"0\" width=\"500\" alt=\"CIMG0803\" src=\"https://farm3.static.flickr.com/2716/4218675472_6e203047b0.jpg\" /></a></p>\n<p>Christopher turns 10 today! He celebrated with a breakfast of pancake cake with whipped cream and strawberry jam <a href=\"http://www.amazon.com/Pancakes-Findus-Pettson-Sven-Nordqvist/dp/190345879X/ref=sr_1_2?ie=UTF8&amp;s=books&amp;qid=1261911328&amp;sr=8-2\">á la Pettson and Findus </a> and continued with the construction of the <a href=\"http://shop.lego.com/product/?p=10194\">LEGO Emerald Night</a>, which took just 2 hours. Next step is to motorize!</p>"},"next":{"fields":{"title":"He's making a list, checking it twice...","date":"23 Dec 2009","slug":"/2009-12-23-hes-making-a-list-checking-it-twice/"}},"previous":{"fields":{"title":"New Sensation","date":"11 Jan 2010","slug":"/2010-01-11-new-sensation/"}}}],"pathPrefix":"","first":false,"last":false,"index":45,"pageCount":1101}}

